<template>
	<div class="search">
		<input type="text" placeholder="" />
		<i class="search-logo"></i>
	</div>
</template>

<script>
	export default {
	}
</script>

<style scoped lang="stylus">
	@import "~common/stylus/index"
	.search{
		display: inline-block;
		position: relative;
		width: 70%;
	}
	.search input{
		overflow: hidden;
		padding-left: 5%;
		outline: none;
		text-decoration: none;
		width: 95%;
		height: 32px;
		background-color: #f0f1f2;
		border-radius: 18px;
	}
	.search-logo{
		position: absolute;
		top: 5px;
		right: 10px;
		width: 20px;
		height: 20px;
		background-image: url(search.png);
		background-size: 20px 20px;
	}
</style>



