<template>
	<div class="TV-detail">
		
		<scroll class="conent" :data="TVlist">
			<div>
				<div class="big-img">
					<img :src="TVlist.hor_w16_pic"/>
					<div class="title-info">
						<h1>{{TVlist.album_name}}</h1>
						<span>{{TVlist.publish_time}}</span>
					</div>
				</div>
				<div class="detail-title">
					<div class="TV-img">
						<img :src="TVlist.ver_small_pic"/>
					</div>
					<div class="title-btn">
						<button class="btn-like">
							<i class="icon-like"></i>
						</button>
						<button class="btn-share">
							<i class="icon-share"></i>
						</button>
						<button class="btn-dl">
							<i class="icon-dl"></i>
						</button>
					</div>
					<div class="information">
						<div class="score">
							评分<span>{{TVlist.score}}</span>
						</div>
						<div class="director">
							导演<span>{{TVlist.director}}</span>
						</div>
						<div class="starring">
							主演<span>{{TVlist.main_actor}}</span>
						</div>
						<div class="type">
							类型<span>{{TVlist.second_cate_name}}</span>
						</div>
					</div>
					<router-view :data="setItem"></router-view>
					<series :data="TVlist" @select="selectItem"></series>
					
					<div class="profile">
						<h3>剧情介绍</h3>
						<p>{{TVlist.video_desc}}</p>
					</div>
				</div>
				<!--明星-->
				<star :data="TVlist.aid"></star>
			</div>
		</scroll>
		<div class="fix-top">
			<div class="fix-img" @click="back">
				<img src="./return.png"/>
			</div>
		</div>
		
	</div>
</template>

<script>
	import Scroll from "base/scroll/scroll"
	import Series from "components/series/series"
	import Star from "components/star/star"
	import axios from "axios"
	import {params,callback,url,ERR_OK} from "api/getTVdetail"
	import {mapActions} from "vuex"
	
	
	export default {
		data(){
			return {
				TVlist:[],
				setItem:null
			}
		},
		props:{
			data:{
				type: Object,
	            default: function() {
	                return {
	                    id: "",
	                    name: "",
	                }
	            }
			}
		},
		components:{
			Star,
			Scroll,
			Series
		},
		created(){
			this._getTVlist();
		},
		methods:{
			selectItem(item,index){
				this.setItem = item;
				this.$router.push(
					{
						path:`/TV/${this.TVlist.aid}/${item.vid}`
					}
				)
			},
			_getTVlist(){
				if(!this.data.vid){
					this.$router.push('/TV')
					return
				}
				params.vid = this.data.vid;
				this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
					if(res.data.statusText == ERR_OK){
						this.TVlist=res.data.data;
//						console.log(this.TVlist)
					}
				})
			},
			back(){
	    		this.$router.back();
	   		},
	   		...mapActions([
	   			'selectPlay'
	   		])
		},
		watch:{
			data(){
				this._getTVlist();
			}
		}
	}
</script>

<style scoped lang="stylus">
	@import "~common/stylus/index";
	.conent{
		position: fixed;
		top: 0;
		height: 100%;
		overflow: hidden;
	}
	.fix-top{
		position: fixed;
		width: 100%;
		height: 40px;
		background: rgba(0,0,0,0.1);
		top: 0;
	}
	.fix-img{
		height: 32px;
		width: 32px;
	}
	.fix-img img{
		margin-top: 4px;
		width: 100%;
		height: 100%;
		display: block;
		color: #fff;
	}
	.big-img{
		height: 150px;
		overflow: hidden;
	}
	.big-img img{
		display: block;
		width: 100%;
		filter: blur(15px);
	}
	.TV-detail{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #fafafa;
	}
	.detail-title{
		position: relative;
		width: 100%;
		margin-top: 10px;
	}
	.TV-img{
		position: absolute;
		right: 20px;
		top: -100px;
	}
	.title-info,.title-btn{
		width: 100%;
		padding: 5px 0;
	}
	.title-info{
		position: absolute;
		top: 30px;
	}
	.title-info h1{
		margin-top: 20px;
		padding-left: 20px;
		line-height: 40px;
		font-size: 20px;
		color: #fff;
	}
	.title-info span{
		font-size: 12px;
		color: #f8f8f8;
		margin-left: 22px;
	}
	.title-btn{
		width: 40%;
		float: right;
		text-align: right;
	}
	.title-btn button{
		margin-top: 60px;
		display: inline-block;
		width: 35px;
		text-align: center;
		cursor: pointer;
		border: none;
		font-size: 12px;
		background: none;
		color: #999;
	}
	.title-btn i{
		display: block;
		width: 14px;
		height: 14px;
		margin: 0 auto 3px;
	}
	.icon-like{
		background: url(like.png) no-repeat;
		background-size: 14px 14px;
	}
	.icon-share{
		background: url(share.png) no-repeat;
		background-size: 14px 14px;
	}
	.icon-dl{
		background: url(download.png) no-repeat;
		background-size: 14px 14px;
	}
	.plot{
		margin-top: 10px;
		border-bottom: 1px solid #e5e5e5;
	}
	.plot-item{
		display: inline-block;
		width: 32%;
		height: 40px;
		line-height: 42px;
		text-align: center;
	}
	.router-link-active.plot-item{
		border-bottom: 1px solid #f05252;
		color: #f05252;
	}
	.information{
		margin-top: 20px;
		font-size: 12px;
		padding-left: 15px;
		padding-bottom: 10px;
		border-bottom: 1px solid #e1e1e1;
	}
	.information div{
		height: 24px;
		line-height: 24px;
		color: #666;
	}
	.information div span{
		padding-left: 20px;
		color: #333;
	}
	.information div.score span{
		color: #D80C18;
	}
	.information div.starring,div.type{
		overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	}
	.profile{
		padding:10px 15px;
		border-bottom: 1px solid #E1E1E1;
	}
	.profile h3{
		margin-top: 10px;
		font-size: 12px;
	}
	.profile p{
		margin-top: 10px;
		line-height: 18px;
		font-size: 12px;
		color: #666;
	}
</style>


