<template>
	<div class="blues-num">
		<span v-for="(item,index) in player" @click="click(item,index)">{{index+1}}</span>
	</div>
</template>
<script>
	import {params,callback,url,ERR_OK} from "api/getPlayer"
	import axios from "axios"
	export default {
		data(){
			return {
				player:[]
			}
		},
		props:{
			data:null
		},
		created(){
			this.$nextTick(()=>{
				this._getPlayer()
			})
		},
		methods:{
			_getPlayer(){
				if(!this.data.aid){
					return
				}
				this.$http.jsonp('http://api.tv.sohu.com/v4/album/videos/'+this.data.aid+'.json',
				{params:params,"jsonp":callback}).then((res)=>{
					this.player = res.data.data.videos;
//					console.log(this.player)
				})
			},
			click(item,index){
				this.$emit('select',item,index);
			}
		},
		watch:{
			data(){
				this.$nextTick(()=>{
					this._getPlayer()
				})
			}
		}
	}
</script>
<style scoped lang="stylus">
	@import "~common/stylus/index";
	.blues-num{
		height: 170px;
		box-sizing: border-box;
		padding: 10px 15px;
		border-bottom: 1px solid #E5E5E5;
		overflow: hidden;
	}
	.blues-num span{
		display: inline-block;
		width: 30px;
		text-align: center;
		font-size: 14px;
		height: 30px;
		line-height: 30px;
	}
	.active{
		background: #E5E5E5;
	}
</style>



