<template>
	<div class="player">
		<div class="player-video">
			<video width="100%" autoplay controls>
				<source :src="SetsUrl.nor" type="video/mp4"></source>
			</video>
		</div>
		<div class="control">
			
		</div>
	</div>
</template>
<script>
	import {params,callback,url} from "api/getSet"
	export default {
		data(){
			return {
				SetsUrl:[]
			}
		},
		props:{
			data:{
				type: Object,
	            default: function() {
	                return {
	                    id: "",
	                    name: "",
	                }
	            }
			}
		},
		created(){
			this._getSet()
		},
		methods:{
			_getSet(){
				if(!this.data.vid){
					return
				}
				params.vid = this.data.vid;
				this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
					this.SetsUrl=res.data.data.urls.mp4;
					console.log(this.SetsUrl)
				})
			}
		},
		watch:{
			data(){
				this._getSet()
			}
		}
	}
</script>
<style scoped lang="stylus">
	@import "~common/stylus/index";
	.player{
		height: 215px;
		margin: 10px 0;
		/*z-index: 100;*/
	}
	video{
		width: 100%;
	}
        
</style>



