export default class TVs{
	constructor({id,name,desc,director,actor,time,
		score,time_length,second_cate_name,vid,ver_w12_pic,
		tip,index}){
		this.id = id;
		this.director = director;
		this.desc = desc;
		this.actor = actor;
		this.name = name;
		this.time = time;
		this.score = score;
		this.time_length = time_length;
		this.second_cate_name = second_cate_name;
		this.vid = vid;
		this.ver_w12_pic = ver_w12_pic;
		this.tip = tip;
		this.index=index
	}
}
export function createTV(TVdata,index){
	return new TVs({
		id:TVdata.aid,
		name:TVdata.album_name,
		desc:TVdata.album_desc,
		director:TVdata.director,
		actor:TVdata.main_actor,
		time:TVdata.publish_time,
		score:TVdata.score,
		time_length:TVdata.time_length,
		second_cate_name:TVdata.second_cate_name,
		vid:TVdata.vid,
		ver_w12_pic:TVdata.ver_w12_pic,
		tip:TVdata.tip,
		index:index
	})
}


