<template>
	<div class="TV">
		<scroll :data="variety" class="TV-conent">
			<div>
				<div class="TV-img" v-for="item in variety">
					<div class="img-list">
						<img v-lazy="item.ver_w12_pic" alt="" />
						<div class="TV-blues">
							<span>{{item.tip}}</span>
						</div>
						<div class="TV-img-bottom">
							<h1 class="TV-title">{{item.name}}</h1>
							<h2 class="TV-subtitle">{{item.time}}</h2>
						</div>
					</div>
				</div>
			</div>
		</scroll>
	</div>
</template>

<script>
	import {params,callback,url,ERR_OK} from "api/getTV"
	import {createTV} from "common/js/createTV"
	import Scroll from "base/scroll/scroll"
	import axios from "axios"
	
	
	//传入TV_ID和OFFSET就可以获取不同的信息,PAGE_SIZE获取多少条数据
	const TV_ID = 32;
	const OFFSET = 24;
	const PAGE_SIZE = 50;
	export default {
		data(){
			return{
				variety:[]
			}
		},
		created(){
			this._getVarietyList();
		},
		components:{
			Scroll
		},
		methods:{
			_getVarietyList(){
				params.subId = TV_ID;
				params.offset = OFFSET;
				params.page_size = PAGE_SIZE;
				this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
					if(res.data.statusText == ERR_OK){
						this.variety = this._processingVarietylist(res.data.data.videos);
					}
//					console.log(this.variety)
				})
			},
			_processingVarietylist(list){
				let ret = [];
				for(let i=0;i<list.length;i++){
					ret.push(createTV(list[i]));
				}
				return ret;
			}
		}
	}
</script>

<style scoped lang="stylus">
	@import "~common/stylus/index";
	@import "~common/stylus/base";
</style>



