<template>
	<div class="stars">
		<div class="star">
			<div class="stars-item" v-for="item in stars" @click="clickItem(item)">
				<div class="stars-link">
					<span class="stars-avatar">
						<img :src="item.image" alt="" />
					</span>
					<span class="stars-name">{{item.name}}</span>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import {params,callback,url,ERR_OK} from "api/getStar"
	import axios from "axios"
	export default {
		data(){
			return {
				stars:[]
			}
		},
		props:{
			data:{
				type:Number,
				default:null
			}
		},
		created(){
			this.starList()
		},
		methods:{
			clickItem(item){
				this.$router.push(
					{
						path:`/TV/${this.data}/${item.id}`
					}
				)
			},
			starList(){
				params.playlistId = this.data;
				this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
					this.stars = res.data.data;
				})
			}
		},
		watch:{
			data(){
				this.starList();
			}
		}
	}
</script>
<style scoped lang="stylus">
	@import "~common/stylus/index";
	.stars{
		margin-top: 10px;
		padding: 10px 0;
	}
	.star{
		height: 94px;
	}
	.stars-item{
		float: left;
		padding: 0 5px;
		width: 20%;
		box-sizing: border-box;
	}
	.stars-link{
		text-align: center;
	}
	.stars-avatar{
		display: inline-block;
		width: 100%;
	}
	.stars-avatar img{
		display: block;
		width: 100%;
		border-radius: 50%;
		box-sizing: border-box;
	}
	.stars-name{
		font-size: 12px;
	}
</style>


