<template>
	<scroll :data="data" class="TV-conent">
		<div>
			<div class="TV-img" v-for="item in data" @click="clickItem(item)">
				<div class="img-list">
					<img v-lazy="item.ver_w12_pic" alt="" />
					<div class="TV-blues">
						<span>{{item.tip}}</span>
					</div>
					<div class="TV-img-bottom">
						<h1 class="TV-title">{{item.name}}</h1>
						<h2 class="TV-subtitle">{{item.time}}</h2>
					</div>
				</div>
			</div>
		</div>
	</scroll>
	
</template>

<script>
	import Scroll from "base/scroll/scroll"
	export default {
		props:{
			data:{
				type:Array,
				default:[]
			}
		},
		components:{
			Scroll
		},
		methods:{
			clickItem(item){
				this.$emit('select',item)
			}
		}
	}
</script>

<style scoped lang="stylus">
	@import "~common/stylus/index";
	@import "~common/stylus/base";
</style>



