<template>
	<div class="TV">
		<list-view :data="TVs" @select="clickItem"></list-view>
		<router-view :data="TVlist"></router-view>
		<div class="loading" v-show="!TVs.length">
			<loading></loading>
		</div>
	</div>
	
</template>

<script>
	import {params,callback,url,ERR_OK} from "api/getTV"
	import {createTV} from "common/js/createTV"
	import axios from "axios"
	import ListView from "base/list-view/list-view"
	import Loading from "base/loading/loading"
	
	
	//传入TV_ID和OFFSET就可以获取不同的信息,PAGE_SIZE获取多少条数据
	const TV_ID = 16;
	const OFFSET = 24;
	const PAGE_SIZE = 50;
	export default {
		data(){
			return{
				TVs:[],
				TVlist:0
			}
		},
		components:{
			ListView,
			Loading
		},
		created(){
			this._getTVlist();
		},
		methods:{
			clickItem(item){
				for(let i=0;i<this.TVs.length;i++){
					if(this.TVs[i].index==item.index){
						this.TVlist = this.TVs[i];
						this.$router.push(
							{
								path:`/TV/${item.id}`
							}
						)
					}
				}
//				console.log(Object.prototype.toString.call(this.TVs[0]))
			},
			_getTVlist(){
				params.subId = TV_ID;
				params.offset = OFFSET;
				params.page_size = PAGE_SIZE;
				this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
					if(res.data.statusText == ERR_OK){
						this.TVs = this._processingTVlist(res.data.data.videos);
					}
				})
			},
			_processingTVlist(list){
				let ret = [];
				for(let i=0;i<list.length;i++){
					ret.push(createTV(list[i],i));
				}
				return ret;
			}
		}
	}
</script>

<style scoped lang="stylus">
	@import "~common/stylus/index";
	@import "~common/stylus/base";
	.loading{
		position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
	}
        
</style>



