<template>
	<div>
		动漫
	</div>
</template>

<script>
	import {params,callback,url,ERR_OK} from "api/getTV"
	import axios from "axios"
	
	//传入TV_ID和OFFSET就可以获取不同的信息,PAGE_SIZE获取多少条数据
	const TV_ID = 39;
	const OFFSET = 24;
	const PAGE_SIZE = 50;
	export default {
		data(){
			return{
				animation:[]
			}
		},
		created(){
			this._getMoviesList();
		},
		methods:{
			_getMoviesList(){
				params.subId = TV_ID;
				params.offset = OFFSET;
				params.page_size = PAGE_SIZE;
				this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
					if(res.data.statusText == ERR_OK){
						this.animation = res.data.data.videos;
					}
					
//					console.log(this.animation)
				})
			}
		}
	}
</script>

<style scoped lang="stylus">
</style>



