import Vue from "vue"
import VueRouter from "vue-router"
import TV from "components/TV/TV"
import Movies from "components/movies/movies"
import Variety from "components/variety/variety"
import Animation from "components/animation/animation"
import MDrama from "components/m-drama/m-drama"
import TVDetail from "components/TV-detail/TV-detail"
import Player from "components/player/player"

Vue.use(VueRouter)
export default new VueRouter({
	routes:[
		{
			path:'/',
			redirect:'/TV'
		},
		{
			path:'/logo',
			redirect:'/TV'
		},
		{
			path:'/TV',
			component:TV,
			children:[
				{
					path:":id",
					component:TVDetail,
					children:[
						{
							path:':player',
							component:Player
						}
					]
				}
			]
		},{
			path:'/movies',
			component:Movies
		},{
			path:'/variety',
			component:Variety
		},{
			path:'/animation',
			component:Animation
		},{
			path:'/m-drama',
			component:MDrama
		}
	]
})



