<template>
	<div class="m-header">
		<router-link class="icon" to="/logo">
			<img src="../../common/image/rabbit.jpg"/>
		</router-link>
		<search class="search"></search>
		<div class="login"></div>
	</div>
</template>

<script>
	import Search from "base/search/search" 
	export default {
		components:{
		  	Search
		  }
	}
</script>

<style scoped lang="stylus">
	.m-header{
		position: relative;
		height: 44px;
		text-align: center;
		color: #f5f5f5;
	}
	.icon{
		position: absolute;
		left: 5px;
		width: 40px;
		height: 34px;
	}
	.icon img{
		width: 100%;
		height: 100%;
	}
	.login{
		position: absolute;
		top: 0;
		right: 0px;
		width: 36px;
		height: 36px;
		background: url(login.png) no-repeat;
		background-size: 28px 28px;
	}
</style>



